export default class Queue {
  items: unknown[];

  constructor() {
    this.items = [];
  }

  enqueue(item: unknown): void {
    this.items.push(item);
  }

  dequeue(): unknown {
    if (this.isEmpty()) return null;
    return this.items.shift();
  }

  isEmpty(): boolean {
    return this.items.length === 0;
  }
}
